import React, {Component} from 'react';
import ItemProduct from './itemProduct';

class ReviewProductsHome extends Component {
    showItem = (item = []) => {
        return item.map((item2,key2) => {
            return <ItemProduct data={item2} key={key2}/>
        })
    }
    showData = () => {
        if (this.props.data) {
            return this.props.data.reviewProduct.map((item,key) => (
                <div key={key} className="latest-prdouct__slider__item">
                    {this.showItem(item)}
                </div>
            ))
        }
    }
    render() {
        return (
            <div className="latest-product__text">
                <h4>Sản phẩm nổi bật</h4>
                <div className="latest-product__slider owl-carousel">
                    {this.showData()}
                </div>
            </div>
        );
    }
}

export default ReviewProductsHome;