import React, {Component} from 'react';
import Home from '../../components/Home';
import {connect} from "react-redux";
import * as Actions from "../../actions";
import {Helmet} from 'react-helmet';
const loadJs = require('loadjs');
const moduleName = 'Organic Fruits';
class Index extends Component {
    componentDidMount() {
        loadJs('/js/main.js');
        this.props.fetchData();
    }
    render() {
        return (
            <div>
                <Helmet>
                    <title>{moduleName}</title>
                </Helmet>
                <Home data={this.props.data} dataDefault={this.props.dataDefault}/>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadHomeData.fetching,
        data: state.payloadHomeData.data,
        error: state.payloadHomeData.error,
        dataDefault: state.payloadDataDefault.data
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: () => dispatch(Actions.homeAction.requestHomeData())
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Index);