import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import NumberFormat from 'react-number-format';

class ItemProduct extends Component {
    render() {
        return (
            <Link to={'/product/'+this.props.data.name_url+'-'+this.props.data.id} className="latest-product__item">
                <div className="latest-product__item__pic">
                    <img src={this.props.data.icon} alt={this.props.data.name} />
                </div>
                <div className="latest-product__item__text">
                    <h6>{this.props.data.name}</h6>
                    <span><NumberFormat value={this.props.data.giaban} displayType={'text'} thousandSeparator={true} suffix={' đ'} /></span>
                </div>
            </Link>
        );
    }
}

export default ItemProduct;