import React, {Component} from 'react';
import ItemNews from '../News/ItemNews';

class NewsHome extends Component {

    showData = () => {
        return this.props.data.latestNews.map((item,key) => {
            return (
                <div key={key} className="col-lg-4 col-md-4 col-sm-6">
                    <ItemNews data={item}/>
                </div>
            )
        })
    };

    render() {
        return (
            <section className="from-blog spad">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="section-title from-blog__title">
                                <h2>Tin tức mới</h2>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        {this.props.data ? this.showData() : ''}
                    </div>
                </div>
            </section>
        );
    }
}

export default NewsHome;