import React, {Component} from 'react';
import SlideCategoryHome from './SLideCategoryHome';
import FeaturedProductsHome from './FeaturedProductsHome';
import BannerHome from './BannerHome';
import LatestProductsHome from './LatestProductsHome';
import TopRatedProductsHome from './TopRatedProductsHome';
import ReviewProductsHome from './ReviewProductsHome';
import NewsHome from './NewsHome';

class Index extends Component {
    render() {
        return (
            <div>
                <SlideCategoryHome data={this.props.dataDefault}/>
                {/*<FeaturedProductsHome/>*/}
                <BannerHome data={this.props.data}/>
                <section className="latest-product spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-4 col-md-6">
                                <LatestProductsHome data={this.props.dataDefault}/>
                            </div>
                            <div className="col-lg-4 col-md-6">
                                <TopRatedProductsHome data={this.props.dataDefault}/>
                            </div>
                            <div className="col-lg-4 col-md-6">
                                <ReviewProductsHome data={this.props.dataDefault}/>
                            </div>
                        </div>
                    </div>
                </section>
                <NewsHome data={this.props.dataDefault}/>
            </div>
        );
    }
}

export default Index;