import React, {Component} from 'react';
import ItemProduct from './itemProduct';
class LatestProductsHome extends Component {

    showItem = (item = []) => {
        return item.map((item2,key2) => {
            return <ItemProduct data={item2} key={key2}/>
        })
    }

    showData = () => {
        return this.props.data.latestProduct.map((item,key) => {
            return (
                <div key={key} className="latest-prdouct__slider__item">
                    {this.showItem(item)}
                </div>
            )
        })
    }

    render() {
        return (
            <div className="latest-product__text">
                <h4>Sản phẩm mới</h4>
                <div className="latest-product__slider owl-carousel">
                    {this.props.data ? this.showData() : ''}
                </div>
            </div>
        );
    }
}

export default LatestProductsHome;