import React, {Component} from 'react';
import {Link} from 'react-router-dom';
class SLideCategoryHome extends Component {
    // showData = () => {
    //     const {categoryProduct} = this.props.data;
    //     return categoryProduct.map((item, key) => {
    //         return (
    //             <div key={key} className="col-lg-3">
    //                 <div className="categories__item set-bg" data-setbg="img/categories/cat-1.jpg">
    //                     <h5><a href="!#">Fresh Fruit</a></h5>
    //                 </div>
    //             </div>
    //         )
    //     });
    // };

    render() {
        const {categoryProduct} = this.props.data;
        return (
            <section className="categories">
                <div className="container">
                    <div className="row">
                        <div className="categories__slider owl-carousel">
                            {
                                categoryProduct.map((item,key) => (
                                    <div key={key} className="col-lg-3">
                                        <div className="categories__item set-bg" data-setbg={item.icon}>
                                            <h5><Link to={'product/'+item.name_url+'-cg'+item.id}>{item.name}</Link></h5>
                                        </div>
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default SLideCategoryHome;